.stars {
  display: flex;
  flex-direction: row;

  &__star {
    width: 15px;
    height: 15px;
    margin-right: 4px;
    background-image: url("../images/star.svg");
    background-position: center;
    background-repeat: no-repeat;
  }
  &--4 {
    div:nth-child(-n + 4) {
      background-image: url("../images/star-active.svg");
    }
  }
}
